<template>
  <div class="search">
    <van-search
      v-model="value"
      placeholder="请输入搜索关键词"
      show-action
      shape="round"
      @input="inputTest"
      @search="onSearch"
      clearable
    >
      <div slot="action" @click="onSearch(1)">搜索</div>
    </van-search>
    <!-- <div v-if="listShow">
      <van-list v-model="loading" :finished="finished">
        <gameList :propList="propData" />
        <van-cell
          v-for="item in list"
          :key="item.label"
          :title="item.label"
          @click="labelClick(item.label)"
        />
      </van-list>
    </div>-->
    <div class="hot-word" v-if="!listShow&&!SearchShow">
      <p>大家正在搜</p>
      <div>
        <van-tag round v-for="item in label" :key="item.dt" @click="labelClick(item)">{{item}}</van-tag>
      </div>
    </div>
    <div class="cl" v-if="SearchShow">
      <ul>
        <li>
          <div :class="[activeClass=='all' ? 'exact' : '']" @click="addExact('all')">
            <span>全部</span>
          </div>
        </li>
        <li>
          <div :class="[activeClass=='game' ? 'exact' : '']" @click="addExact('game')">
            <span>游戏</span>
          </div>
        </li>
        <li>
          <div :class="[activeClass=='soft' ? 'exact' : '']" @click="addExact('soft')">
            <span>软件</span>
          </div>
        </li>
      </ul>
      <p class="count">
        共搜索到
        <span>{{pageInfo.totalRows}}</span>个结果
      </p>
      <div class="game-list">
        <van-list v-model="loading" :finished="finished" class="van-clearfix">
          <van-cell v-for="item in list" :key="item.contentId">
            <img :src="item.icon" class="img-left" />
            <div class="text-left">
              <p>{{item.title}}</p>
              <p class="text-detail">
                <!-- <span class="download">500+下载</span> -->
                <span
                  class="size"
                >{{item.size=='null'?'':Math.floor(item.size/1024 * 100) / 100+'M'}}</span>
              </p>
            </div>
            <div class="gift">
              <van-button round color="#f1f0f6">下载</van-button>
            </div>
          </van-cell>
        </van-list>
      </div>
      <van-pagination
        v-model="pageInfo.curPage"
        :total-items="Number(pageInfo.totalRows)"
        :show-page-size="5"
        force-ellipses
        @change="onSearch"
      />
    </div>
  </div>
</template>

<script>
import { Search, Tag, List, Cell, Pagination, Button } from "vant";
import axios from "axios";
import gameList from "./gameList";
require("../../untils/mm");
export default {
  components: {
    [Search.name]: Search,
    [List.name]: List,
    [Cell.name]: Cell,
    [Pagination.name]: Pagination,
    [Button.name]: Button,
    [Tag.name]: Tag,
    gameList
  },
  data() {
    return {
      value: "",
      list: [],
      label: [],
      loading: false,
      finished: true,
      listShow: false,
      activeClass: "all",
      SearchShow: false,
      pageInfo: {
        curPage: 1,
        totalPage: "10",
        totalRows: "0"
      },
      propData: {
        price: "",
        appuid: "",
        contentId: "",
        ver: "",
        size: "",
        title: "",
        icon: "",
        orderurl: ""
      }
    };
  },
  mounted() {
    // mm.init({
    //   showtitle: true, //true：显示；false：隐藏
    //   channelid: "5410093632", //
    // });
    // mm.download("300009240030", false);
    var that = this;
    window.addEventListener(
      "popstate",
      function() {
        that.value = "";
        that.list = [];
        that.SearchShow = false;
      },
      false
    );
    axios
      .post(process.env.VUE_APP_API + "searchHotKeyworld.do", {
        params: this.label.length
      })
      .then(response => {
        this.label = response.data.data;
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  methods: {
    labelClick(label) {
      this.value = label;
      this.onSearch();
    },
    onSearch(search) {
      console.log("12", this.value);
      console.log("search", search);
      if (search == 1) {
        this.pageInfo.curPage = 1;
      }
      if (this.value != "") {
        axios
          .post(process.env.VUE_APP_API + "search.do", {
            keyword: this.value,
            currentPage: this.pageInfo.curPage
          })
          .then(response => {
            console.log("response");
            window.parent.history.pushState({}, "", "#/search?" + this.value);
            this.listShow = false;
            this.SearchShow = true;
            this.list = response.data.data.blocks;
            this.pageInfo = response.data.data.pageInfo;
            this.pageInfo.curPage = Number(this.pageInfo.curPage);
            // mm.download("300009240030", true);
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    inputTest() {
      if (this.value == "") {
        this.list = [];
        this.listShow = false;
        this.SearchShow = false;
      }
    },
    // onInput() {
    //   console.log("onInput", this.value);
    //   if (this.value != "") {
    //     this.listShow = true;
    //     this.SearchShow = false;
    //     axios
    //       .post(process.env.VUE_APP_API + "search.do", {
    //         keyword: this.value,
    //         currentPage: 1
    //       })
    //       .then(response => {
    //         console.log("response");
    //         if (this.listShow) {
    //           // this.list = response.data.data;
    //           this.propData = response.data.data.blocks[0];
    //         } else {
    //           this.list = [];
    //         }
    //       })
    //       .catch(function(error) {
    //         console.log(error);
    //       });
    //   } else {
    //     this.list = [];
    //     this.listShow = false;
    //     this.SearchShow = false;
    //   }
    // },
    addExact(a) {
      console.log("a", a);
      this.activeClass = a;
    }
  }
};
</script>

<style lang="less">
body {
  background-color: #fff;
}

.search {
  .hot-word {
    margin: 0 12px;
    p {
      font-size: 12px;
      color: #1c1c1c;
    }
    .van-tag--default {
      height: 29px;
      line-height: 29px;
      padding: 0 15px;
      margin: 0 9px 9px 0;
      background-color: #f1f1f1;
      font-size: 12px;
      color: #7b7b7d;
      border-radius: 14.5px;
    }
  }
  .van-search__action {
    font-size: 15px;
    font-family: "PingFang";
    color: rgb(0, 0, 0);
  }
  .van-search__content {
    background-color: #f1f1f1;
  }
}
.cl {
  width: 100%;
  ul {
    height: 44px;
    li {
      div {
        font-size: 14px;
        font-family: "PingFang-SC-Medium";
        float: left;
        margin-left: 20px;
        width: 28px;
        line-height: 44px;
        text-align: center;
        background-color: #fff;
        color: #000;
      }

      .exact {
        color: #005bdb;
      }
    }
  }

  .count {
    margin: 0;
    padding: 0 20px;
    height: 32.5px;
    line-height: 32.5px;
    background: #eee;
    font-size: 11px;
    color: #7b7b7d;
    span {
      color: #005dbd;
    }
  }
  .exact span {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      bottom: -13px;
      // left: 20px;
      height: 2px;
      width: 27.5px;
      background-color: #005bdb;
      transform: translate(-50%, 0);
      margin-left: -50%;
    }
  }
}
.van-list {
  // margin: 10px;
  height: 100%;
  width: 100%;
  .text-left {
    // height: 80px;
    margin: 2px 0 0 9px;
    max-width: 210px;
    float: left;
    line-height: 45px;
    font-size: 15px;
    font-family: "PingFang-SC-Medium";
    p {
      margin: 0;
      line-height: 32px;
    }
    .text-detail {
      font-size: 11px;
      color: #7b7b7d;
      line-height: 11px;
    }
  }
}

.img-left {
  float: left;
  height: 56px;
  width: 56px;
  border-radius: 12.5px;
}

.van-cell {
  padding: 12.5px 12px;
}

.game-list {
  margin-bottom: 2px;
}
</style>
